.location {
	.content {
		width: 100%;

		#map {
			width: 100%;
			height: 50vh;
			filter: saturate(0.6);
		}
	}

	.map-info-list {
		display: flex;
		padding: 60px 32px 0;
		margin-bottom: 80px;
		gap: 60px;

		.map-info-item {
			width: 240px;

			strong {
				display: inline-block;
				@include font(16px);
				font-weight: 500;
				margin-bottom: 8px;
			}

			address {
				@include font(13px, 1.4);
				font-weight: 300;
				color: #777;
				margin-bottom: 32px;
			}

			a {
				@include font(12px);
				font-weight: 500;
				display: block;
				color: #777;
				margin-bottom: 32px;
			}

			button {
				@include font(13px);
				text-transform: capitalize;
				text-decoration: underline;
			}
		}
	}
}
