@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;900&display=swap');

* {
	padding: 0;
	margin: 0;
	font-family: 'Poppins';
	box-sizing: border-box;
	font-weight: 400;
}

ul,
li,
ol {
	list-style: none;
}

a {
	color: inherit;
	text-decoration: none;
	cursor: pointer;
}

button {
	background: none;
	border: 0;
	cursor: pointer;
}

button,
input,
select,
textarea {
	background-color: transparent;
	border: 0;

	&:focus {
		outline: none;
		box-shadow: none;
	}
}

address {
	font-style: normal;
}

fieldset {
	border: 0;
}
